import Vue from 'vue';
import Component from 'vue-class-component';


@Component
export class TagHelper extends Vue {
    createTag() {
        const name = window.prompt('');
        if (!name) {
            return window.alert('');
        } else if (name.length > 3) {
            return window.alert('');
        }
        this.$store.commit('createTag', name);
        window.alert('');
    }
}

export default TagHelper;